<template>
  <div class="container">

       <ViewChat
        v-if="showChat"
        :chatList = chatList
        :actualState = actualState
        :answers = answers
        :disableAnswers="disableAnswers"
        @pushmessage = pushToChatList()
        @action = doAction($event)
        @actionanswer = doAction($event)
        @deleteanswer = deleteanswer($event)
      />

      <InfoModal
        v-if="showModal"
        :modalInfo = modalInfo
        @action = doAction($event)
      />

    </div>
</template>

<script>
import InfoModal from '@/components/InfoModal.vue'
import ViewChat from '@/components/ViewChat.vue'
import MainData from '@/data/data.json'

export default {
  name: 'App',
  data() {
    return {
      stepNumber: 0,
      showModal: false,
      showImageModal: false,
      modalInfo: {},
      modalImageData: {},
      showChat: true,
      disableAnswers: "",
      answers: [],
      chatList: [],
      mainBranch: MainData,
      actualState: {}
    }
  },
  components: {
    InfoModal,
    ViewChat
  },
  methods: {
    sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms))
        },

    async addMessages(talk, callback) {      
      for (let i = 0; i < talk.length; i++) {
        this.chatList.push(talk[i])
        await this.sleep(1000)
      }
      callback()
    },

    doAction(action) {
      if ((action.type === "next")&&(this.stepNumber < this.mainBranch.length-1)) {
        this.stepNumber += 1
        this.actualState = this.mainBranch[this.stepNumber]
        this.showModal = false
        if (this.answers.length > 0) {
          this.answers.length = 0
        }
        this.doAction(this.actualState)
      }
      else if ((action.type === "close")){
        this.showModal = false
      }
      else if ((action.type === "closeCheck")){
        this.showModal = false
        this.doAction(action.action)
      }
      else if ((action.type === "info")){
        this.modalInfo = action.data
        this.showModal = true
      }
      else if ((action.type === "answer")){
        this.actualState = action
        for (let i = 0; i < this.actualState.data.answers.length; i++) {
          this.answers.push(this.actualState.data.answers[i])
        }
      }
      else if ((action.type === "talk")){
        this.disableAnswers = "disable-answer"
        this.addMessages(action.data.talk, ()=> {
          if (action.data.action.type === "next"){
            this.answers.length = 0
          }
          this.doAction(action.data.action)
          this.disableAnswers = ""          
          })
      }
      else if ((action.type === "checkAnswerList")){
        if (this.answers.length === 0) {
          this.doAction({"type": "next"})
        }
      }
      else if ((action.type === "jumpToStep")){
        this.stepNumber = action.stepNumber
        this.actualState = this.mainBranch[action.stepNumber]
        this.showModal = false
        this.doAction(this.actualState)
      }
      else if ((action.type === "jumpToStepClearChat")){
        this.stepNumber = action.stepNumber
        this.actualState = this.mainBranch[action.stepNumber]
        this.showModal = false
        this.chatList.length = 0
        this.doAction(this.actualState)
      }
      else if ((action.type === "openLink")){
        window.open(action.url, '_blank')
      }
    },

    deleteanswer(index) {
      this.answers.splice(index,1)
    }
  },
  created() {
    this.actualState = this.mainBranch[this.stepNumber]
    this.doAction(this.actualState)
  }
}
</script>

